import React from "react";
import Card from "./card.js";

class Cards extends React.Component{
    constructor(props){
        super(props)
        this.state ={card: "sd", focus:["unfocus","focused","unfocus"]}
        this.cards = ["ft","sd","td"];
        this.pos = 1;
    }

    handleClick = e =>{
        this.setCard(e.target.className);
        this.setFocus();
    }

    setCard(button){
        if(button == "previous_btn" && this.pos>0){
            let nowCard = this.cards[this.pos-1];
            this.setState({
                card: nowCard,
                focus: this.setFocus(nowCard)
            })
            this.pos--;
        }else if(button == "next_btn" && this.pos<2){
            let nowCard = this.cards[this.pos+1];
            this.setState({
                card: nowCard,
                focus: this.setFocus(nowCard)
            })
            this.pos++;
        }
    }

    setFocus = (card) =>{
        switch(card){
            case "ft":
                    return ["focused","unfocus","unfocus"]
                break;
            case "sd":
                    return ["unfocus","focused","unfocus"]
                break;
            case "td":
                    return ["unfocus","unfocus","focused"]
                break;
        }
    }

    render(){
        var classes = "cards "+this.state.card+" "+this.props.blur;
        return(
      <section className={classes}>
        <Card tit="Happy-surfing" color="orange" focus={this.state.focus[0]} position="previous"/>
        <Card tit="Weather" color="purple" focus={this.state.focus[1]} position="center"/>
        <Card tit="N. 18" color="blue" focus={this.state.focus[2]} position="next"/>
        <div className="slide_btns">
          <span className="previous_btn" onClick={this.handleClick}></span>
          <span className="next_btn" onClick={this.handleClick}></span>
        </div>
      </section>
        )
    }
}

export default Cards;