import React from "react"
import Browser from "./components/browser"
import "./happy.css"

function Happy(props){
    return(
        <div className="happyApp" align="center">
            <div className="language" align="center">
                <p>Browser Happy is also available in English.</p>
                <a href="#">Click here to change the language to English.</a>
            </div>
            <header className="App_header" align="center">
                <h1>NAVEGA</h1><h1 className="amarillo">FELIZ</h1><br/>
                <p>Conectado. No tengas miedo.</p><b>¡Actualiza hoy tu navegador!</b>
            </header>
            <section className="browsers">
                <Browser name="GOOGLE CHROME"/>
                <Browser name="MOZILLA FIREFOX"/>
                <Browser name="SAFARI"/>
                <Browser name="OPERA"/>
                <Browser name="INTERNET EXPLORER"/>
            </section>
            <footer>
                <div className="titles">
                    <strong>QUÉ ES NAVEGA FELIZ</strong>
                    <strong>COMPARTE TU FELICIDAD</strong>
                    <span className="icon twitter"></span>
                    <span className="icon facebook"></span>
                </div>
                <p>
                    Usar un navegador obsoleto hace inseguro tu ordenador.
                    Navega feliz es un modo de que descubras las últimas versiones de
                    los principales navegadores disponibles. También puedes conocer 
                    navegadores alternativos que se adaptan a ti incluso mejor que el 
                    que estas usando actualmente.
                </p>
            </footer>
        </div>
    )
    
}
export default Happy