import React from "react";

function User_Card(props){
    return(
        <div className={"userCard "+props.display} align="center">
            <span className="user_img"></span>
            <strong>Javier Martinez</strong>
            <hr/>
            <p>University: UnipoliDGO</p> <br/>
            <p>Group: 4°A</p> <br/>
            <p>Career: Software Development</p> <br/>
            <p>Professor: Octavio Mejia</p> <br/>
            <hr/>
            <span className="close"></span>
        </div>
    )
}

export default User_Card