import React from 'react'
import "./weather.css"
import Day from "./assets/day.png"
import Night from "./assets/night.png"
import Cloudy from "./assets/cloudy.png"
import Cards from './components/cards'

class Capsule_strong extends React.Component{
   render(){
      return(
        <div id={this.props.id} className="header_component" align="center"><strong>{this.props.content}</strong></div>
      )
   }
}

class Weather extends React.Component{

    constructor(props){
        super(props)
        this.state = {bgs: [Day, Cloudy, Night], selected: Day}
        this.pos = 0
    }
    changeBG = () =>{
        let selected = this.state.bgs[this.pos]
        this.setState({
            selected: selected
        })
    }

    componentDidMount(){
        setInterval(() => this.changeBG(), 1000)
    }

   render(){
    var hour = 13;
    var minutes = 12;
    var time = hour+":"+minutes;
        return(
            <div>
                <img id="background" src={this.state.selected} height="100%" width="100%"/> 
                <header className="weather-header" align="center">
                    <Capsule_strong content={time} id="hour"/>
                    <Capsule_strong content="Weather" id="tittle"/>
                    <Capsule_strong content="Durango" id="location"/>
                </header>
                <Cards/>
            </div>
        )
   }
}
export default Weather