import React from "react";
import Purple from "../assets/purple.png";
import Orange from "../assets/orange.png";
import Blue from "../assets/blue.png";
import { Link } from "react-router-dom";
function get_description(tit){
    switch(tit){
        case "Weather":
            return "A simple weather view service, with details per day and hour."
            break;
        case "Happy-surfing":
            return "A info site for select your default or favorite explorer."
            break;
        case "N. 18":
            return "This have not a objetive, it's only for practice and fun."
            break;
    }
}

function get_bg(color){
    switch(color){
        case "purple":
            return Purple;
            break;
        case "orange":
            return Orange;
            break;
        case "blue":
            return Blue;
            break;  
    }
}

class card extends React.Component{
    
    constructor(props){
        super(props)
        this.state = {display: "hidden"};
    }

    getAppForRender(name){
        switch(name){
            case "Weather":
                return Blue;
                break;
            case "Happy-surfing":

                break;
            case "N. 18":
                return Orange;
                break;
        }
    }

    handleClick = e =>{
        if(this.state.display == "hidden"){
            this.setState({
                display: "show"
            })
        }else{
            this.setState({
                display: "hidden"
            })
        }
    }

    render(){
        var classes = ("card "+this.props.focus+" "+this.props.color+" "+this.props.position);
        var app_classes = ("app "+this.state.display);
        return(
        <div className={classes}>
            <p>{this.props.tit}</p> <br/>
            <p className="apps_description">{get_description(this.props.tit)}</p> <br/>
            <Link to={"/"+this.props.tit} className="button" app={this.props.tit} display={this.state.display} onClick={this.handleClick}>Show</Link>
            
        </div>            
        )
    }
}



export default card;