import React from "react"

class Browser extends React.Component{
    constructor(props){
        super(props)
        this.state = {to: "toOpen"}
    } 
    
    setData(){
        var data = {
            description: "",
            version: "",
            site: "",
            likes: "",
        };
        switch(this.props.name){
            case "GOOGLE CHROME":
                data.description = '"Un nuevo y rápido navegador de Google. ¡Pruébalo ahora!"';
                data.version = "37";
                data.site= "https://www.google.com/intl/es-419/chrome/";
                data.likes = "17"
                break;
            case "MOZILLA FIREFOX":
                data.description = '"tu seguridad online es la principal prioridad de Firefox. Firefox es gratuito y hecho para sacar el mayor partido posible a la web."';
                data.version = "32";
                data.site= "https://www.mozilla.org/es-MX/firefox/new/";
                data.likes = "17"
                break;
            case "SAFARI":
                data.description = '"Safari, de Apple, para Mac y Windows. El navegador más innovador del mundo."';
                data.version = "7.1";
                data.site= "https://www.google.com/intl/es-419/chrome/";
                data.likes = "0"
                break;
            case "OPERA":
                data.description = '"El navegador más rapído de la Tierra. Seguro, potente y facil de usar. Con una exelente protección de la privacidad. Y gratis."';
                data.version = "24";
                data.site= "https://www.opera.com/es/download";               
                data.likes = "10"
                break;
            case "INTERNET EXPLORER":
                data.description = '"Diseñado para ayudarte a tomar el control de tu privacidad y navegar con seguridad. Gratis, de Microsoft."';
                data.version = "11";
                data.site= "https://www.microsoft.com/es-mx/download/internet-explorer.aspx";
                data.likes = "2.1"
                break;
        }

        return data
    }

    render(){
        var data = this.setData();
        return(
            <div className="browser" align="center">
                <span className={this.props.name +" img "+this.state.to}></span> <br/>
                <strong>{this.props.name}</strong>
                <p>{data.description}</p>
                <p>{"Ultima versión: "+data.version}</p> <br/>
                <div><a href={data.site} target="_blank">Visita la web para más información</a></div>
                <button className="like"><span></span>Me gusta<p>{" "+data.likes+" mill."}</p></button>
            </div>
        )
    }
}

export default Browser